






































import { Component, Vue, Provide } from 'vue-property-decorator'
import wjhJS from '../util/wjhJS.js'
import designBlack from '../components/designBlank.vue'
import uploadPic from '../components/uploadPic.vue'
import Axios from 'axios'

@Component({
  components: {
    designBlack,
    uploadPic
  }
})
export default class StyleCenter extends Vue {
  columns1 = [{
    title: '样式编号',
    key: 'id'
  },
  {
    title: '样式名称',
    key: 'name'
  },
  {
    title: '创建时间',
    key: 'createTime',
    render (h: any, params: any) {
      return h('span', wjhJS.dateFormat(parseInt(params.row.createTime + '000'), 'y-M-dd hh:m'))
    }
  },
  {
    title: '操作',
    key: 'action',
    width: 180,
    align: 'center',
    render: (h: any, params: any) => {
      return h('div', [
        h('Button', {
          props: {
            type: 'success',
            size: 'small'
          },
          attrs: {
            ghost: ''
          },
          style: {
            marginRight: '5px'
          },
          on: {
            click: () => {
              // this.remove(params.index)
              this.view(params.row.id)
            }
          }
        }, '预览')
      ])
    }
  }
  ]
  data1: object[] = []
  nickName: string = ''
  modal1: boolean = false
  modal1Pic: string = ''
  @Provide() viewData: fixStyleConstruction = {
    name: '',
    bg: '',
    answerBg: '',
    singleFont: '',
    singlePic: '',
    singleMusic: '',
    fontAndPic: '',
    fontAndMusic: '',
    picAndMusic: '',
    resultBackground: ''
  }
  @Provide() pictureUpload: fixStyleConstruction = {
    name: '',
    bg: '',
    answerBg: '',
    singleFont: '',
    singlePic: '',
    singleMusic: '',
    fontAndPic: '',
    fontAndMusic: '',
    picAndMusic: '',
    resultBackground: ''
  }
  // ts包裹ref选择器
  _ref (s: string) {
    const ref: any = this.$refs[s]
    return ref
  }

  showModal (data: string) { // 展示大图片用的
    this.modal1 = true
    this.modal1Pic = data
  }

  openDesign () {
    this._ref('design').show = true
  }

  urlChange (rdx: rdx, res: string) {
    this.pictureUpload[rdx] = res
    console.log(this.pictureUpload[rdx])
  }

  view (index: number) {
    console.log(index)
    this.$axios.get('/evaluate-miniapp/styles/find', { params: { id: index } }).then((res: any) => {
      this._ref('viewTable').show = true
      console.log(res.data)
      this.viewData = res.data.data
    })
    console.log(this.data1)
  }

  // 提交数据
  submitData () {
    this.pictureUpload.name = this.nickName
    for (let i in this.pictureUpload) {
      if (!this.pictureUpload[i]) {
        this.$Notice.warning({
          title: `${i} 不能为空（任意内容都不能为空）`
        })
        return
      }
    }
    const copy = Object.assign(this.pictureUpload)
    this.$axios.post('/evaluate-miniapp/styles/add', this.pictureUpload).then((res: any) => {
      this.$Notice.success({
        title: `添加样式成功！`
      })
      this._ref('design').show = false
      console.log(res)
      this.getData()
      this._ref('viewTable').show = false
    })
  }

  getData () {
    this.$root.load = true
    this.$axios.get('/evaluate-miniapp/styles').then((res: any) => {
      this.$root.load = false
      this.data1 = res.data.data
      console.log(res.data.data)
    })
  }

  mounted () {
    this.getData()
    /* this.data1.push({
      id: this.data1.length + 1,
      name: '样式名称',
      createTime: new Date().getTime()
    }) */
  }
}
