import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import Axios from 'axios'

declare module 'vue/types/vue' { // 模块补充 可以去这个路径查阅
  interface Vue {
    url: string,
    qiniuToken: any,
    load: boolean,
    $axios: any,
    [key:string]: any // 解决对象的遍历
  }
}

const myAxios = Axios.create({
  baseURL: 'https://api.psy-1.com'
})

Vue.use(iView)
Vue.config.productionTip = false
Vue.prototype.$axios = myAxios

new Vue({
  data () {
    return {
      url: 'http://web.debug.yetaowang.com/cosleep/evaluate',
      load: false,
      qiniuToken: ''
    }
  },
  router,
  store,
  created () {
    this.$axios.get('/evaluate-miniapp/upload/token').then((res: any) => {
      console.log(res.data.data.token)
      this.qiniuToken = res.data.data.token
    })
  },
  render: h => h(App)
}).$mount('#app')
