<template>
  <div class="resultLogic">
    <div>默认结果文案：<br/><Input v-model="defaultDesc" type="textarea" style="width: 300px;margin-bottom: 10px"/></div>
    是否显示结果框：
    <Select placement="bottom" v-model="showBorder" size="small" placeholder="是否显示结果框" style="width:100px">
      <Option value="1" key="1">是</Option>
      <Option value="0" key="0">否</Option>
    </Select>
    <Divider dashed />
    <!-- <div class="add" @click="add">添加<Icon type="ios-add-circle-outline" color="#2d8cf0" /></div> -->
    <div class="block" v-for="(li, index) in itemList" :key="li.random">
      <Button style="position: absolute;right: 20px;top: 0px;" @click="minus(index)" type="error">删除</Button>
      <div class="score">区间min：&nbsp;&nbsp;<InputNumber :max="2000" :min="-2000" v-model="li.min"></InputNumber></div>
      <div class="score">区间max：<InputNumber :max="2000" :min="-2000" v-model="li.max"></InputNumber></div>
      <div class="desc">结果标题：<Input :rows="6" size="small" type="textarea" v-model="li.title" style="max-width: 300px"/></div>
      <div class="desc">结果文案：<Input :rows="6" size="small" type="textarea" v-model="li.desc" style="max-width: 300px"/></div>
      <div class="desc">微信orApp分享标题：<Input size="small" placeholder="不填则选取全局的分享标题" type="textarea" v-model="li.shareTitle" style="max-width: 300px"/></div>
      <div class="desc">微信orApp分享描述：<Input size="small" placeholder="不填则选取全局的分享描述" type="textarea" v-model="li.shareDesc" style="max-width: 300px"/></div>
      <uploadPic @urlChange="urlChange" :idx="index" :res="li.background"/> （结果图片不上传 则使用默认样式）
      <Divider dashed />
    </div>
  </div>
</template>

<script>
import uploadPic from '../components/uploadPic.vue'
export default {
  name: 'resultLogic',
  data () {
    return {
      defaultDesc: '',
      itemList: [],
      showBorder: '0'
    }
  },
  methods: {
    add () {
      this.itemList.push({
        random: Math.random(),
        min: 0,
        max: 0,
        desc: '',
        background: '',
        title: '',
        shareTitle: '',
        shareDesc: ''
      })
    },
    minus (index) {
      this.itemList.splice(index, 1)
    },
    urlChange (rdx, res) {
      this.itemList[rdx].background = res
    }
  },
  components: {
    uploadPic
  },
  mounted () {
    console.log('resultLogic mounted')
  }
}
</script>

<style lang="scss" scoped>
.resultLogic {
  position: relative;
  left: 12px;
  .add {
    display: inline-block;
    margin-right: 20px;
    cursor: pointer;
  }
  .minus {
    display: inline-block;
    cursor: pointer;
  }
  .block {
    position: relative;
    .score {
      display: inline-block;
      margin-right: 10px;
      margin-bottom: 10px;
    }
    .desc {
      display: flex;
      margin-bottom: 10px;
    }
  }
}
</style>
