<template>
  <div class="uploadPic">
    <div class="demo-upload-list" v-for="(item, index) in uploadList" :key="index">
      <template v-if="item.status === 'finished'">
        <img :src="item.url">
        <div class="demo-upload-list-cover">
          <Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
          <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
        </div>
      </template>
      <template v-else>
        <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
      </template>
    </div>
    <Upload
      ref="upload"
      :show-upload-list="false"
      :default-file-list="defaultList"
      :on-success="handleSuccess"
      :format="['jpg','jpeg','png', 'gif']"
      :max-size="2048"
      :on-format-error="handleFormatError"
      :on-exceeded-size="handleMaxSize"
      :before-upload="handleBeforeUpload"
      multiple
      type="drag"
      :data="{token: this.$root.qiniuToken}"
      action="https://upload.qiniup.com"
      style="display: inline-block;width:58px;">
      <div style="width: 58px;height:58px;line-height: 58px;">
        <Icon type="ios-camera" size="20"></Icon>
      </div>
      <!-- "http(s)://upload-z2.qiniup.com" -->
    </Upload>
    <Modal title="图片查看" v-model="visible">
      <img :src="largePic" v-if="visible" style="width: 100%">
    </Modal>
  </div>
</template>
<script>
//  <!-- http://192.168.88.21:3001/upload -->
import qiniu from 'qiniu-js'

export default {
  props: ['urlLink', 'idx', 'res'],
  data () {
    return {
      defaultList: [],
      imgName: '',
      visible: false,
      uploadList: [],
      largePic: ''
    }
  },
  methods: {
    handleView (name) {
      this.imgName = name
      this.visible = true
    },
    handleRemove (file) {
      const fileList = this.$refs.upload.fileList
      this.$refs.upload.fileList.splice(fileList.indexOf(file), 1)
      this.$emit('urlChange', this.idx, '')
    },
    handleSuccess (res, file) {
      console.log(res)
      this.largePic = `https://res.psy-1.com/${res.key}`
      this.$emit('urlChange', this.idx, `https://res.psy-1.com/${res.key}`)
      file.url = `https://res.psy-1.com/${res.key}`
      file.name = res.key
    },
    handleFormatError (file) {
      this.$Notice.warning({
        title: '文件格式不正确',
        desc: '文件 ' + file.name + ' 不正确, 请上传 jpg，png 或 gif(gif 将变成静态图片).'
      })
    },
    handleMaxSize (file) {
      this.$Notice.warning({
        title: '文件size超出范围',
        desc: '文件  ' + file.name + ' 太大了，控制在小于2M以下'
      })
    },
    handleBeforeUpload () {
      const check = this.uploadList.length < 1
      if (!check) {
        this.$Notice.warning({
          title: '仅能上传一张图片，请先把图片移除'
        })
      }
      return check
    }
  },
  created () {
    if (this.res) {
      this.largePic = this.res
      this.defaultList.push({
        url: this.largePic,
        name: '123'
      })
      return
    }
    if (this.urlLink && !this.res) {
      this.largePic = this.urlLink
      this.defaultList.push({
        url: this.largePic,
        name: '123'
      })
    }
    if (this.res && !this.urlLink) {
      this.largePic = this.res
      this.defaultList.push({
        url: this.largePic,
        name: '123'
      })
    }
  },
  mounted () {
    console.log('???', this.res)
    this.uploadList = this.$refs.upload.fileList
    console.log(this.uploadList)
  }
}
</script>
