




































































import { Component, Vue, Provide, Model, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Design from '@/components/design.vue' // @ is an alias to /src
import wjhJS from '../util/wjhJS.js'
import resultLogin from '../components/resultLogic.vue'
