// 选择题
import SingleFont from '../typeChoice/singleFont.vue'
import SinglePic from '../typeChoice/singlePic.vue'
import FontAndPic from '../typeChoice/fontAndPic.vue'

@Component({
  components: {
    HelloWorld,
    Design,
    SingleFont,
    SinglePic,
    FontAndPic,
    resultLogin,
    uploadPic
  }
})
export default class Home extends Vue {
  columns1 = [{
    title: '问卷编号',
    key: 'id'
  },
  {
    title: '问卷标题',
    key: 'title'
  },
  {
    title: '创建时间',
    key: 'createTime',
    sortable: true,
    render (h: any, params: any) {
      return h('span', wjhJS.dateFormat(params.row.createTime, 'y-M-dd hh:m'))
    }/* ,
    filterMultiple: true,
    filterMethod (value: any, row: any) {
      return false
    } */
  },
  {
    title: '发布时间',
    key: 'publishTime',
    sortable: true,
    render (h: any, params: any) {
      return h('span', wjhJS.dateFormat(parseInt(params.row.publishTime), 'y-M-dd hh:m'))
    }
  },
  {
    title: '发布状态',
    key: 'publishState',
    render (h: any, params: any) {
      return params.publishState ? h('span', '是') : h('span', '否')
    }
  },
  {
    title: '发布平台',
    key: 'publishPlatform'
  },
  {
    title: '问卷访问量',
    key: 'pv',
    sortable: true
  },
  {
    title: '问卷收集量',
    key: 'solveCount',
    sortable: true
  },
  {
    title: '操作',
    key: 'action',
    width: 180,
    align: 'center',
    render: (h: any, params: any) => {
      return h('div', [
        h('Button', {
          props: {
            type: 'success',
            size: 'small'
          },
          attrs: {
            ghost: ''
          },
          style: {
            marginRight: '5px'
          },
          on: {
            click: () => {
              window.open(`${this.$root.url}?article_id=${params.row.id}#/`, '_blank')
              // this.remove(params.index)
            }
          }
        }, '查看'),
        h('Button', {
          props: {
            type: 'primary',
            size: 'small'
          },
          style: {
            marginRight: '5px'
          },
          attrs: {
            ghost: ''
          },
          on: {
            click: () => {
              this.edit(params.index, params.row.id)
            }
          }
        }, '编辑'),
        h('Button', {
          props: {
            type: 'error',
            size: 'small'
          },
          attrs: {
            ghost: ''
          },
          on: {
            click: () => {
              this.remove(params.index, params.row.id)
            }
          }
        }, '删除')
      ])
    }
  }
  ]
  lazyLoadPic = false // 解决渲染 不显示图片的问题
  advertisementUrl = '' // 广告链接
  advertisementPic = '' // 广告图片
  copyData1 = [] // 复制data1 用于搜索器过滤
  searchKey = ''
  value1 = '1' // 开关选项 左侧
  @Provide() groupsArray: any[] = ['next', 'end', '0'] // 默认选项的选择 清空数据的时候 也需要相同
  title = '' // 问卷标题
  desc = '' // 问卷描述
  weixinDesc = '' // 微信分享desc
  weixinTitle = '' // 微信分享title
  weixinPic = '' // 微信分享的图片
  editMode: boolean = false // 是否为编辑模式
  currentEditId: any = 0 // 临时的编辑id
  @Provide() styleIdList: any[] = [] // 样式的套数
  @Provide() styleId: number = 26 // 样式
  @Provide() data1: tableGroup[] = []
  @Provide() typeList: object[] = [{ label: '文字单选题 +', value: 'SingleFont' }, { label: '图片单选题 +', value: 'SinglePic' }, { label: '文字&图片单选题 +', value: 'FontAndPic' }] // { label: '音频单选题 +', value: 'SingleMusic' }, { label: '文字&图片单选题 +', value: 'FontAndPic' }, { label: '文字&音频单选题 +', value: 'FontAndMusic' }, { label: '图片&音频单选题 +', value: 'PicAndMusic' }
  @Provide() selection: object[] = [{ name: 'SinglePic', random: Math.random(), editData: {} }] // { name: 'SinglePic', random: Math.random(), editData: {} } 单个数据

  @Watch('searchKey')
  searchChange (val: String) {

  }

  // 编辑数据
  edit (index: any, id: any = '') {
    this.lazyLoadPic = false
    this.$root.load = true
    this.editMode = true
    this.$axios.get('/evaluate-miniapp/question/find', { params: { id } }).then((res: any) => {
      console.log(res.data.data)
      this.currentEditId = res.data.data.id
      const resultLoginData = this._ref('resultLogin')
      resultLoginData.itemList = []
      this.title = res.data.data.title
      this.desc = res.data.data.desc
      console.log(
        this.groupsArray,
        res.data.data.selections
      )

      this.groupsArray = ['next', 'end', '0'] // 修正选项
      for (let i = 1; i < res.data.data.selections.length; i++) {
        this.groupsArray.push(i.toString())
      }

      this.selection = [] // 清空当前的数据先
      for (let i of res.data.data.selections) {
        this.selection.push({ name: i.type, random: Math.random(), editData: i })
      }
      // this.selection = res.data.data.selection
      resultLoginData.defaultDesc = res.data.data.defaultDesc
      resultLoginData.itemList = JSON.parse(res.data.data.itemList)
      resultLoginData.showBorder = res.data.data.visiableFrame ? '1' : '0'

      this.styleId = parseInt(res.data.data.styleId) // 样式id

      this._ref('design').show = true
      this.weixinDesc = res.data.data.weixinDesc
      this.weixinTitle = res.data.data.weixinTitle
      this.weixinPic = res.data.data.weixinPic

      this.advertisementPic = res.data.data.advertisementPic // res.data.data.advertisementPic
      this.advertisementUrl = res.data.data.advertisementUrl
      this.$root.load = false

      this.lazyLoadPic = true
    })
  }

  // 结果面板控制的 增加按钮
  resultLoginAdd () {
    this._ref('resultLogin').add()
  }

  remove (index: any, id: any = '') {
    this.$axios.post('/evaluate-miniapp/question/del', { id }).then((res: any) => {
      if (res.data.msg === '成功') {
        this.data1.splice(index, 1)
        this.$Notice.success({
          title: `删除成功`
        })
      } else {
        this.$Notice.error({
          title: `服务器错误`
        })
      }
    })
    console.log(index, id)
  }

  // 删除选择题
  deleteSelection (index: number) {
    this.selection.splice(index, 1)
    this.groupsArray.pop()
    console.log(this.selection)
  }

  // 打开设计页面 但是要清空数据
  openDesign () {
    this.weixinDesc = ''
    this.weixinTitle = ''
    this.weixinPic = ''
    this.advertisementUrl = ''
    this.advertisementPic = ''
    this.lazyLoadPic = false
    this.editMode = false
    this.groupsArray = ['next', 'end', 0] // 清空选项
    const resultLoginData = this._ref('resultLogin')
    resultLoginData.itemList = [{
      random: Math.random(),
      min: 0,
      max: 0,
      desc: '',
      background: ''
    }]
    this._ref('design').show = true
    this.title = ''
    this.desc = ''
    this.selection = [
      { name: 'SinglePic', random: Math.random(), editData: {} }
    ]
    resultLoginData.defaultDesc = ''
    this.$nextTick(() => {
      this.lazyLoadPic = true
    })
  }

  // ts包裹ref选择器
  _ref (s: string) {
    const ref: any = this.$refs[s]
    return ref
  }

  // 添加选择题
  addSelection (s: string) {
    this.$Message.success(`添加${s}成功`)
    this.groupsArray.splice(this.groupsArray.length, 0, this.groupsArray.length - 2)
    this.selection.push({ name: s, random: Math.random(), editData: {} })
  }

  // 数据不能为空
  notNull (data: any, whiteList: any = []) {
    for (let i in data) {
      if (whiteList.includes(i)) {
        return true
      }
      if (data[i] === 0) { // 0被判断为false
        return true
      }
      if (!data[i]) {
        if (i === 'background') { // 上次失误写错的nibu代码 可改
          return true
        }
        this.$Notice.warning({
          title: `${i} 不能为空（任意内容都不能为空）`
        })
        return false
      }
    }
    return true
  }

  // 搜索功能
  searchFunc (v: any) {
    // this.copyData1 已 复制
    const result = this.copyData1.filter((value: any, index: any) => {
      return value.title.includes(v) // 标题搜索
    })
    this.data1 = result
    console.log(result)
  }

  // 图片上传
  urlChange (rdx: any, res: string) {
    this[rdx] = res
  }

  // 输出总数据
  outPutData () {
    let selections: any[] = []
    const resultLoginData = this._ref('resultLogin')
    this._ref('selections').forEach((v: any) => {
      selections.push(JSON.parse(v.outPutData()))
    })

    for (let i of resultLoginData.itemList) { // 优化 大小的 顺序
      if (i.min > i.max) {
        const save = i.max
        i.max = i.min
        i.min = save
      }
    }

    let result: fixSubjectConstruction = {
      title: this.title,
      desc: this.desc,
      selections: selections,
      defaultDesc: resultLoginData.defaultDesc,
      visiableFrame: resultLoginData.showBorder,
      advertisementPic: this.advertisementPic,
      advertisementUrl: this.advertisementUrl,
      itemList: JSON.stringify(resultLoginData.itemList),
      styleId: this.styleId,
      weixinDesc: this.weixinDesc,
      weixinTitle: this.weixinTitle,
      weixinPic: this.weixinPic
    }

    // 数据不能为空
    if (resultLoginData.itemList.length < 1) {
      this.$Notice.warning({
        title: `选择题不能为空`
      })
      return
    }

    for (let i of selections) {
      if (!this.notNull(i, ['subjectImg'])) {
        return
      }
      if (i.selections.length === 0) {
        this.$Notice.warning({
          title: `选择题选项不能为空`
        })
        return
      }
      for (let i2 of i.selections) {
        console.log(i2, '检测singleFont为什么不会触发')
        if (!this.notNull(i2)) {
          return
        }
      }
    }

    for (let i of resultLoginData.itemList) {
      if (!this.notNull(i, ['shareTitle', 'shareDesc', 'background'])) {
        return
      }
    }

    // 检测数据不为空 的成功回调
    if (this.notNull(result)) {
      const itemListToStr: any = JSON.stringify(result.itemList)
      const selectionsToStr: any = JSON.stringify(result.selections)
      result.itemList = itemListToStr
      result.selections = selectionsToStr
      console.log(result)

      // 修改问卷的时候需要用到的
      if (this.editMode) {
        result.id = this.currentEditId
      }
      // this.editMode ? result.id = this.currentEditId : ''
      this.$axios.post(this.editMode ? '/evaluate-miniapp/question/edit' : '/evaluate-miniapp/question/add', result).then((res: any) => {
        this.$Notice.success({
          title: this.editMode ? `修改问卷成功` : `添加问卷成功！`
        })
        this.getData() // 重新刷新一次问卷
        this._ref('design').show = false // 关闭编辑页面
        console.log(res)
      })
    }
  }

  getData () {
    this.$root.load = true
    this.$axios.get('/evaluate-miniapp/question').then((data: any) => {
      this.$root.load = false
      this.data1 = data.data.data
      this.copyData1 = data.data.data // 备份 用于搜索
    })
  }

  // 获取样式套数
  getStyleList () {
    this.$axios.get('/evaluate-miniapp/styles').then((res: any) => {
      for (let i of res.data.data) {
        this.styleIdList.push({
          value: i.id,
          label: i.name
        })
      }
      console.log(res.data.data, 'style o1')
    })
  }

  mounted () {
    /* this.data1.push({
      id: 1,
      title: '喵喵喵',
      createTime: 1,
      publishTime: 1,
      publishState: true,
      publishPlatform: '微信',
      pv: 100,
      solveCount: 100
    }) */
    this.getStyleList()
    this.getData()
  }
}
