















import { Component, Vue, Provide, Model, Watch, Prop, Emit } from 'vue-property-decorator'
import UploadPic from '../components/uploadPic.vue'

@Component({
  props: {

  },
  components: {
    UploadPic
  }
})
export default class FontAndPic extends Vue {
  @Prop(Array) readonly groupsArray!: any
  @Prop(Number) readonly indexKey!: any
  @Prop(Object) readonly editData!: any // 从外面传进来的 编辑数据
  columns1 = [{ title: '序号',
    key: 'id'
  }, { title: '内容',
    key: 'content',
    render: (h: any, all: any) => {
      return h('Input', {
        props: {
          clearable: true,
          placeholder: '内容（最长21字，否则溢出）',
          value: all.row.content
        },
        on: {
          input: (val: any) => {
            all.row.content = val
            this.data1[all.index] = all.row
          }
        }
      })
    } }, { title: '图片',
    key: 'picUrl',
    render: (h: any, all: any) => {
      return h(UploadPic, {
        props: {
          urlLink: this.data1[all.index].picUrl,
          idx: all.index
        },
        on: {
          urlChange: this.picUrlChange
        }
      })
    } }, { title: '得分（请输入数字）',
    key: 'score',
    render: (h: any, all: any) => {
      return h('InputNumber', {
        props: {
          clearable: true,
          placeholder: '得分',
          value: all.row.score,
          number: true
        },
        on: {
          input: (val: any) => {
            if (/^\d+$/.test(val)) {
              all.row.score = val
              this.data1[all.index] = all.row
            }
          }
        }
      })
    } }, {
    title: '选取后跳转id',
    key: 'toId',
    render: (h: any, params: any) => {
      return h('Select', {
        props: {
          value: this.data1[params.index].toId // this.data1[params.index].toId // 之前是默认是自身的 现在默认为next
        },
        on: {
          'on-change': (value: any) => {
            this.data1[params.index].toId = value
          }
        }
      },
      this.groupsArray.map((type: any) => {
        return h('Option', {
          props: { value: type }
        }, isNaN(parseInt(type)) ? type : parseInt(type) + 1)
      }))
    }
  }
  ]
  content = ''; title = ''; name = '文字&图片单选题'
  subjectImg = '' // 题目的展示图片
  lazyLoad = true // 编辑的时候需要lazyLoad
  @Provide() data1: fontAndPic[] = []

  addNullItem () {
    console.log(this.data1, 'start data1')
    this.data1.push({
      id: this.data1.length + 1,
      content: '',
      picUrl: '',
      score: 0,
      toId: 'next' // this.indexKey 默认Next
    })
    console.log(this.data1, 'end data1')
  }

  minusNullItem () {
    this.data1.pop()
  }

  // 子组件图片改变
  picUrlChange (index: any, res: any) {
    console.log(this.data1)
    this.data1[index].picUrl = res
    console.log(index, res)
    console.log('change!!!~~')
  }

  // 图片上传
  urlChange (rdx: any, res: string) {
    this[rdx] = res
  }

  outPutData () {
    const data = {
      type: 'FontAndPic', // 当前类的名称
      title: this.title,
      content: this.content,
      selections: this.data1,
      subjectImg: this.subjectImg
    }
    return JSON.stringify(data)
  }

  // 编辑的时候 需要从外部获取数据
  initDataFromProps () {
    if (JSON.stringify(this.editData) !== '{}') {
      this.lazyLoad = false
      this.title = this.editData.title
      this.content = this.editData.content
      this.subjectImg = this.editData.subjectImg
      // this.data1 = this.editData.selections 这里我想直接替换掉 但是不能 应该是props的机制的问题了 下次考虑可以传入 json 让vue不进行任何的watcher
      for (let i of this.editData.selections) {
        this.data1.push({
          id: i.id,
          content: i.content,
          picUrl: i.picUrl,
          score: i.score,
          toId: i.toId
        })
      }
      this.$nextTick(() => {
        this.lazyLoad = true
      })
    }
  }

  @Emit('delete')
  deleteSelection (s: number) {

  }

  mounted () {
    console.log('fontAndPic upload')
    this.initDataFromProps()
    // this.addNullItem()
  }
}
